"use client";

import React, { useState, useMemo } from "react";
import { InputGroup } from "../CalculatorUtils";
import { formatRupee } from "../../UI/Utility";

const RetirementCalculator = () => {
  // ---------------- MODE ----------------
  const [mode, setMode] = useState("sip"); // sip | lumpsum

  // ---------------- INPUT STATES ----------------
  const [currentAge, setCurrentAge] = useState(27);
  const [retirementAge, setRetirementAge] = useState(60);
  const [lifeExpectancy, setLifeExpectancy] = useState(80);

  const [monthlyExpense, setMonthlyExpense] = useState(15000);
  const [existingSavings, setExistingSavings] = useState(0);

  const [inflationRate, setInflationRate] = useState(6);
  const [preRetReturn, setPreRetReturn] = useState(15);
  const [postRetReturn, setPostRetReturn] = useState(8);

  // ---------------- CALCULATION ----------------
  const {
    yearsToRetire,
    yearsInRetirement,
    annualExpenseAtRetirement,
    requiredCorpus,
    monthlySavingRequired,
    lumpsumRequiredToday,
  } = useMemo(() => {
    const CA = Number(currentAge);
    const RA = Number(retirementAge);
    const LE = Number(lifeExpectancy);
    const ME = Number(monthlyExpense);
    const ES = Number(existingSavings);

    const i = inflationRate / 100;
    const rPre = preRetReturn / 100;
    const rPost = postRetReturn / 100;

    const yrsToRetire = RA - CA;
    const yrsInRetirement = LE - RA;

    if (
      yrsToRetire <= 0 ||
      yrsInRetirement <= 0 ||
      ME <= 0 ||
      rPost <= i ||
      rPre <= 0
    ) {
      return {
        yearsToRetire: 0,
        yearsInRetirement: 0,
        annualExpenseAtRetirement: 0,
        requiredCorpus: 0,
        monthlySavingRequired: 0,
        lumpsumRequiredToday: 0,
      };
    }

    // 1️⃣ Expense at retirement (inflation adjusted)
    const futureMonthlyExpense =
      ME * Math.pow(1 + i, yrsToRetire);

    const annualExpense = futureMonthlyExpense * 12;

    // 2️⃣ Retirement corpus (Angel One model)
    const corpus =
      annualExpense *
      (1 -
        Math.pow(
          (1 + i) / (1 + rPost),
          yrsInRetirement
        )) /
      (rPost - i);

    // 3️⃣ Existing savings growth
    const existingAtRetirement =
      ES * Math.pow(1 + rPre, yrsToRetire);

    const netCorpusNeeded = Math.max(
      0,
      corpus - existingAtRetirement
    );

    // 4️⃣ SIP calculation
    const monthlyRate = rPre / 12;
    const months = yrsToRetire * 12;

    const sip =
      netCorpusNeeded *
      (monthlyRate /
        (Math.pow(1 + monthlyRate, months) - 1));

    // 5️⃣ Lumpsum required today
    const lumpsumToday =
      netCorpusNeeded /
      Math.pow(1 + rPre, yrsToRetire);

    return {
      yearsToRetire: yrsToRetire,
      yearsInRetirement: yrsInRetirement,
      annualExpenseAtRetirement: Math.round(annualExpense),
      requiredCorpus: Math.round(corpus),
      monthlySavingRequired: Math.round(sip),
      lumpsumRequiredToday: Math.round(lumpsumToday),
    };
  }, [
    mode,
    currentAge,
    retirementAge,
    lifeExpectancy,
    monthlyExpense,
    existingSavings,
    inflationRate,
    preRetReturn,
    postRetReturn,
  ]);

  // ---------------- UI ----------------
  return (
    <div className="bg-white dark:bg-slate-900 p-8 rounded-xl border border-slate-200 dark:border-slate-800 shadow-xl space-y-10">
      
      {/* HEADER + MODE */}
      <div className="flex justify-between items-center">
        <h3 className="text-2xl font-bold text-slate-900 dark:text-white">
          Retirement Planning Calculator
        </h3>

        <div className="flex border rounded-lg overflow-hidden">
          <button
            onClick={() => setMode("sip")}
            className={`px-4 py-2 text-sm font-semibold ${
              mode === "sip"
                ? "bg-indigo-600 text-white"
                : "bg-transparent text-slate-600 dark:text-slate-300"
            }`}
          >
            SIP
          </button>
          <button
            onClick={() => setMode("lumpsum")}
            className={`px-4 py-2 text-sm font-semibold ${
              mode === "lumpsum"
                ? "bg-indigo-600 text-white"
                : "bg-transparent text-slate-600 dark:text-slate-300"
            }`}
          >
            Lumpsum
          </button>
        </div>
      </div>

      <div className="grid lg:grid-cols-2 gap-10">
        {/* INPUTS */}
        <div className="space-y-6">
          <InputGroup label="Current Age" value={currentAge} setter={setCurrentAge} min={18} max={70} unit="Yrs" />
          <InputGroup label="Retirement Age" value={retirementAge} setter={setRetirementAge} min={40} max={80} unit="Yrs" />
          <InputGroup label="Life Expectancy" value={lifeExpectancy} setter={setLifeExpectancy} min={60} max={100} unit="Yrs" />
          <InputGroup label="Current Monthly Expense" value={monthlyExpense} setter={setMonthlyExpense} min={5000} max={300000} step={1000} unit="₹" />
          <InputGroup label="Existing Retirement Savings" value={existingSavings} setter={setExistingSavings} min={0} max={50000000} step={50000} unit="₹" />
          <InputGroup label="Expected Inflation Rate" value={inflationRate} setter={setInflationRate} min={3} max={15} step={0.5} unit="%" />
          <InputGroup label="Return (Before Retirement)" value={preRetReturn} setter={setPreRetReturn} min={5} max={20} step={0.5} unit="%" />
          <InputGroup label="Return (After Retirement)" value={postRetReturn} setter={setPostRetReturn} min={3} max={12} step={0.5} unit="%" />
        </div>

        {/* RESULTS */}
        <div className="p-6 rounded-xl border border-slate-200 dark:border-slate-800 space-y-6">
          <p>Years until Retirement: <b>{yearsToRetire}</b></p>
          <p>Years in Retirement: <b>{yearsInRetirement}</b></p>

          <p className="text-indigo-600 dark:text-indigo-400 font-semibold">
            Annual Income Required After Retirement:
            <br />
            {formatRupee(annualExpenseAtRetirement)}
          </p>

          <div className="pt-4 border-t">
            <p className="text-sm">Required Retirement Corpus</p>
            <p className="text-3xl font-bold text-indigo-700 dark:text-indigo-300">
              {formatRupee(requiredCorpus)}
            </p>
          </div>

          {mode === "sip" ? (
            <div className="pt-4 border-t">
              <p className="text-sm">Monthly SIP Required</p>
              <p className="text-3xl font-bold text-indigo-700 dark:text-indigo-300">
                {formatRupee(monthlySavingRequired)}
              </p>
            </div>
          ) : (
            <div className="pt-4 border-t">
              <p className="text-sm">One-time Lumpsum Required Today</p>
              <p className="text-3xl font-bold text-indigo-700 dark:text-indigo-300">
                {formatRupee(lumpsumRequiredToday)}
              </p>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default RetirementCalculator;
